<template>
    <div class='tabs'>
        <ul class='tablist'>
            <li v-for="(item,index) in list" :key='index' 
                :class='active==index?"li_chose":null'
                @click='choseTab(item,index)'
            >{{item.name}}</li>
        </ul>
        <div class='tabshow'>
            <slot></slot>
        </div>
        
    </div>
</template>
<script>
export default {
    data(){
        return{
           active:0,
        }
    },
    mounted(){
        this.active = this.autoActive;
    },
    props:{
         autoActive:{//设置此参数为默认打开的tab
            default:0,
            type:Number,
         },
        list:{//传入的list
            default:()=>{
                return  [
                    {id:0,name:'天赋树'},
                    {id:0,name:'勋章'},    
                ]
            },
            type:Array,
        }
    },
    methods:{
        choseTab(item,index){
            this.active = index;
            this.$emit('activeTab',{item:item,index:index,active:this.active})
        },
    },
}
</script>
<style lang="less" scoped>
    @import '../../common/css/pulic.less';
    .tabs{
        width:100%;
        height:100%;
        padding:10px 6px 0 6px;
        .box-sizing(@border-box);
        .tablist{
            width:100%;
            height:57px;
            display:flex;
            padding-left: 8px;
            .box-sizing(@border-box);
            li{
                width:158px;
                height:48px;
                text-align: center;  
                font-size:27px; 
                // border-left:4px #104b50 solid;
                // border-top:4px #104b50 solid;
                // border-right:4px #104b50 solid;
                line-height: 48px;
                color:white;
                text-shadow:2px 2px 2px 2px #0887c3;
                margin-left:4px;
                background:url('./img/tab_normal.png') 0 0 no-repeat no-repeat;
                background-size:100% 100%;
                margin-top:9px;
            }
            .li_chose{
              height:57px;
              width:159px;
              line-height: 57px; 
                margin-top:0px;
              background:url('./img/tab_chose.png') 0 0 no-repeat no-repeat;
              background-size:100% 100%;
            }
        }
        .tabshow{
            width:733px;
            height:calc(100% - 66px);
            background:white;
            border:1px #104350 solid;
            margin:0 auto;
            overflow-y: auto;
        }

    }
</style>


