<template>
     <tabs :list='list'  @activeTab='activeChange'>
        <div class='slots'>
            <ul class='bag_lists'>
                <li class='bag_li'>
                    <div class='bag_div'>
                        <img src="" alt="">
                    </div>
                    <span class='bag_grads'>30</span>
                </li>
            </ul>
        </div>
     </tabs>
</template>
<script>
import tabs  from '@/components/tab/tab';
export default {
    components:{tabs},
  data(){
      return{
          activeTab:0,
          list:[
                {id:0,name:'全部'},
                {id:1,name:'精灵'},
                {id:2,name:'装备'},
                {id:3,name:'道具'}, 
            ],
      }
  }, 
  methods:{
        activeChange(obj){
           this.activeTab = obj.active;
        },
  }, 
}
</script>
<style lang="less" scoped>
    @import '../../../common/css/pulic.less';
    .slots{
        width:100%;
        height:100%;
        .bag_lists{
            width:100%;
            padding:0 10px 0 10px;
            .box-sizing(@border-box);
            display:flex;
            justify-content: space-between;
            .bag_li{
                width:123px;
                height:122px;
                border:1px #1b004c solid;
                background:#eee220;
                margin-top:13px;
                .border-radius(12px,12px);
                padding:4px 4px;
                position:relative;
                .box-sizing(@border-box);
                .bag_div{
                    width:100%;
                    height:100%;
                    background:black;
                     .border-radius(12px,12px);
                     img{
                         display:block;
                         width:100%;
                         height:100%;
                         background:red;
                     }
                }
                .bag_grads{
                    width:25px;
                    height:18px;
                    color:white;
                    font-size:18px;
                    position:absolute;
                    bottom:4px;
                    right:4px;
                    line-height: 18px;
                    text-align: center;
                    font-weight: bold;
                }
            }
        }
    }
</style>


