<template>
    <ul class='chose_race' >
        <li v-for='(item,index) in list' :key='index' @click='choseShowTypeFun(item,index)'>
            <img :class='choseShowType==index?"chose_btn":null' :src="item.imgUrl" alt="">
        </li>
    </ul>
</template>
<script>
export default {
    data(){
        return{
            choseShowType:this.defaultChose,
        }
    },
    props:{
        list:{
            type:Array,
            default:()=>{
                return [
                   {id:0, imgUrl:require('@/assets/img/home/sprinte/all_btn.png'),name:'所有'},
                   {id:1, imgUrl:require('@/assets/img/home/sprinte/thunder_btn.png'),name:'雷族'},
                   {id:2, imgUrl: require('@/assets/img/home/sprinte/wood_btn.png'),name:'木族'},
                   {id:3, imgUrl: require('@/assets/img/home/sprinte/fire_btn.png'),name:'火族'},
                   {id:4, imgUrl: require('@/assets/img/home/sprinte/water_btn.png'),name:'水族'},
                   {id:5, imgUrl: require('@/assets/img/home/sprinte/holy_btn.png'),name:'黑曜'},
                   {id:6, imgUrl: require('@/assets/img/home/sprinte/disaster_btn.png'),name:'灾曜'},
                ]
            }
        },
        defaultChose:{
            type:Number,
            default:0,
        },  
    },
    computed:{
    },
    methods:{
        choseShowTypeFun(item,index){
            this.choseShowType = index;
            this.$emit('choseShowTypeFun',{item:item,index:index})
        },
    },
}
</script>
<style lang="less" scoped>
  @import '../../common/css/pulic.less';
    .chose_race{
        height:102px;
        width:714px;
        display:flex;
        background:#10404d;
        .border-radius(51px,51px);
        position:absolute;
        padding:0 6px 0 8px;
        .box-sizing(@border-box);
        //align-content: flex-start;
        justify-content: space-around;
        bottom:13px;
        left:10px;
        z-index: 200;
        li{
            width:85px;
            height:85px;
            margin-top:8.5px;
            img{
                display:block;
                width:100%;
                height:100%;
                .border-radius(42.5px,42.5px); 
            }
            .chose_btn{
                box-shadow: 0 0 8px 4px #fff000;
                -o-box-shadow: 0 0 8px 4px #fff000;
                -ms-box-shadow: 0 0 8px 4px #fff000;
                -moz-box-shadow: 0 0 8px 4px #fff000;
                -webkit-box-shadow: 0 0 8px 4px #fff000;
            }
        }
    }
</style>


